.intro_logo {
  width: 150px;
  -webkit-filter: contrast(115%) brightness(105%);
          filter: contrast(115%) brightness(105%);
}
.intro__title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  font-size: 4em;
  color: white;
  font-weight: 600;
  line-height: 1.2;
}
.intro__link {
  font-size: 1.5em;
  margin: 20px;
  padding: 0.4em 1.3em;
  border-radius: 3em;
  border: 0.2em solid var(--p-color-dark);
  background-color: var(--p-color-dark);
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  color: white;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.intro__link:hover {
  background-color: white;
  color: var(--p-color-dark);
}

.headerMenu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-bottom: 3vh;
}
.headerMenu__link {
  margin: 1em;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  color: white;
}
.headerMenu__link:first-child {
  margin-right: auto;
}
.headerMenu__link_main {
  padding: 0.6em 1.3em;
  border-radius: 3em;
  border: 0.3em solid var(--p-color-dark);
  background-color: var(--p-color-dark);
  -webkit-transition: background-color 0.2s cubic-bezier(0.75, 0, 0.125, 1),
    color 0.3s cubic-bezier(0.75, 0, 0.125, 1);
  transition: background-color 0.2s cubic-bezier(0.75, 0, 0.125, 1),
    color 0.3s cubic-bezier(0.75, 0, 0.125, 1);
}
.headerMenu__link_main:hover {
  background-color: white;
  color: var(--p-color-dark);
}
.headerMenu__link_transparent {
  padding: 0.6em;
  background-color: transparent;
  border-bottom: 0.1em solid transparent;
  -webkit-transition: border-bottom-color 0.2s ease-in-out;
  transition: border-bottom-color 0.2s ease-in-out;
}
.headerMenu__link_transparent:hover {
  border-bottom-color: white;
}

